import WrapOperations from "./wrapOperations"
import WrapOperationTag from "./wrapOperationTag"

export default function () {
  return {
    wrapComponents: {
        operations: (Original, { React }) => (props) => {
            return <WrapOperations {...props} />
        },
        OperationTag: (Original, { React }) => (props) => {
            return <WrapOperationTag {...props} />
        }
    }
  }
}
