import React from "react"
import PropTypes from "prop-types"

export default class NavLink extends React.Component {
    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,

        tagObj: PropTypes.object.isRequired,
        tag: PropTypes.string.isRequired,
        position: PropTypes.number.isRequired
    }

    constructor(props) {
        super(props)
        this.state = {
            open: false,
            position: props.position
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.scrollTo = this.scrollTo.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    listenToScroll() {
        
        let {
            tagObj,
            tag,
            position
        } = this.props 

        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop

        var minPos = -1
        tagObj.get("operations").map(op => {
            let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
            let isShownKey = ["operations", tag, operationId]
            var offset = document.getElementById(isShownKey.join("-")) ? document.getElementById(isShownKey.join("-")).offsetTop : 0
            
            if (minPos === -1) {
                minPos = offset
            } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
                minPos = offset
            }
        })

        
        this.setState({
            open: position === document.getElementById("operations-tag-wrapper-" + tag).offsetTop,
            position: minPos
        })
    }

    scrollTo() {
        const id = "operations-tag-wrapper-" + this.props.tag
        window.scrollTo({
            top: (document.getElementById(id) ? document.getElementById(id).offsetTop : (document.body.scrollTop || document.documentElement.scrollTop)),
            left: 0,
            behavior: 'smooth'
        })
    }


    render() {
        let {
            tag,
            tagObj,
            getComponent
        } = this.props

        const NavSubLink = getComponent("NavSubLink")

        let { open, position } = this.state
        
        return <div className="outer navigation-link tag">
            <div className={open ? "navigation-link tag header open" : "navigation-link tag header"} onClick={this.scrollTo}>
                <span className="navigation-linktag">{ tag }</span>
            </div>
            {open &&
                <div className="navigation-link tag body">
                    {tagObj.get("operations").map(op => {
                        const path = op.get("path")
                        const method = op.get("method")
                        return <NavSubLink 
                            key={`${path}-${method}`}
                            op={op}
                            position={position}
                            tag={tag}
                        />
                    }).toArray()
                    }
                </div>    
            }
        </div>
    }
}