import React from "react"
import PropTypes from "prop-types"


export default class NavSubLink extends React.Component {
    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,

        op: PropTypes.object.isRequired,
        position: PropTypes.number.isRequired,
        tag: PropTypes.string.isRequired,
    }

    constructor(props) {
        super(props)
        
        // window currently at position of this method
        this.state = {
            selected: false
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.scrollTo = this.scrollTo.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    listenToScroll() {
        let {
            op,
            position,
            tag
        } = this.props

        let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
        let isShownKey = ["operations", tag, operationId]
        this.setState({
            selected: position === document.getElementById(isShownKey.join("-")).offsetTop
        })
    }

    scrollTo() {
        let { op, tag } = this.props
        
        let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
        let isShownKey = ["operations", tag, operationId]
        
        window.scrollTo({ 
            top: document.getElementById(isShownKey.join("-")).offsetTop - 45, 
            left: 0, 
            behavior: 'smooth'
        })
    }

    render() {
        let {
            op
        } = this.props

        let {
            deprecated
        } = op.get("operation").toJS()

        let method = op.get("method")

        let {
            selected
        } = this.state
        

        var title = op.getIn(["operation", "title"]) || op.getIn(["operation", "summary"]) || ""
        
        if (title.length <= 1) {
            title = op.get("path")
        }

        return <div className={"navigation-link method" + (selected ? " selected" : "")} onClick={this.scrollTo}>
            <span className="navigation-linktag--sub">{ title }</span>
            {deprecated && <span className="navigation-linktag--sub deprecated">deprecated</span>}
            <div className={`method-icon ${deprecated ? "deprecated" : method} ${selected ? "selected" : ""}`}>
                <span className="navigation-linktag--sub chip">{ method.toUpperCase() }</span>
            </div>
        </div>
    }
}