import React from "react"
import PropTypes from "prop-types"
import { ExpandLess, ExpandMore } from '@material-ui/icons'

export default class ModelContainer extends React.Component {
    static propTypes = {
        collapsedContent: PropTypes.any,
        expanded: PropTypes.bool,
        children: PropTypes.any,
        title: PropTypes.element,
        modelName: PropTypes.string,
        classes: PropTypes.string,
        onToggle: PropTypes.func,
        hideSelfOnExpand: PropTypes.bool,
    }

    static defaultProps = {
        collapsedContent: "{...}",
        expanded: false,
        title: null,
        onToggle: () => { },
        hideSelfOnExpand: false
    }

    constructor(props, context) {
        super(props, context)

        let { expanded, collapsedContent } = this.props

        this.state = {
            expanded: expanded,
            collapsedContent: collapsedContent || ModelCollapse.defaultProps.collapsedContent
        }
    }

    componentDidMount() {
        const { hideSelfOnExpand, expanded, modelName } = this.props
        if (hideSelfOnExpand && expanded) {
            // We just mounted pre-expanded, and we won't be going back..
            // So let's give our parent an `onToggle` call..
            // Since otherwise it will never be called.
            this.props.onToggle(modelName, expanded)
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.expanded !== nextProps.expanded) {
            this.setState({ expanded: nextProps.expanded })
        }
    }

    toggleCollapsed = () => {
        if (this.props.onToggle) {
            this.props.onToggle(this.props.modelName, !this.state.expanded)
        }

        this.setState({
            expanded: !this.state.expanded
        })
    }

    getModelName = (ref) => {
        if (ref.indexOf("#/definitions/") !== -1) {
            return ref.replace(/^.*#\/definitions\//, "")
        }
        if (ref.indexOf("#/components/schemas/") !== -1) {
            return ref.replace(/^.*#\/components\/schemas\//, "")
        }
    }

    getRefSchema = (model) => {
        let { specSelectors } = this.props

        return specSelectors.findDefinition(model)
    }

    render() {
        const { displayName, name, schema } = this.props
        let $$ref = schema && schema.get("$$ref")

        // If we weren't passed a `name` but have a ref, grab the name from the ref
        if (!name && $$ref) {
            name = this.getModelName($$ref)
        }
        // If we weren't passed a `schema` but have a ref, grab the schema from the ref
        if (!schema && $$ref) {
            schema = this.getRefSchema(name)
        }

        let { expanded } = this.state

        return <div id={`model-${name}`} className="model-container" >
            <div className="model-header" onClick={this.toggleCollapsed} >
                <div className="icon">
                    {expanded ? <ExpandLess /> : <ExpandMore />}
                </div>
                <h1 className="model-title">{displayName || name}</h1>
            </div>
            {expanded && this.props.children}
        </div>
    }
}
