import React from "react"
import PropTypes from "prop-types"

export default class ModelNavLink extends React.Component {
    static propTypes = {
        position: PropTypes.number.isRequired
    }

    constructor(props) {
        super(props)
        this.state = {
            open: false,
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.scrollTo = this.scrollTo.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    listenToScroll() {
        let { position } = this.props

        this.setState({
            open: document.getElementById("models") ? (position === document.getElementById("models").offsetTop) : false,
        })
    }

    scrollTo() {
        const id = "models"
        window.scrollTo({
            top: (document.getElementById(id) ? document.getElementById(id).offsetTop : (document.body.scrollTop || document.documentElement.scrollTop)),
            left: 0,
            behavior: 'smooth'
        })
    }


    render() {   
        let { open } = this.state

        return <div className="navigation-link tag">
            <div className={open ? "navigation-link tag header open" : "navigation-link tag header"} onClick={this.scrollTo}>
                <span className="navigation-linktag">Models</span>
            </div>
                   </div>
    }
}